import navT from "../../components/nav/nav.vue"
import {addressEdit,addressDetail} from "@/api/user.js"
export default {
	components:{navT},
	data() {
		return {
			name:"",
			phone:"",
			detail:"",
			sex:"先生",
			address:"请选择送餐地址",
			obj:{},
			id:0
		}
	},
	
	methods: {
		click(){
			addressEdit({
				address:{
					province:this.obj.Province,
					city:this.obj.City,
					district:this.obj.Country
				},
				is_default:0,
				real_name:this.name,
				post_code:"",
				phone:this.phone,
				detail:this.address+this.detail,
				id:this.id
				
			}).then(data=>{
				
				if(data.data.msg=="ok"){
					console.log(12)
					uni.navigateBack({
						delta:1
					})
				}
				console.log(data)
				
			})
			console.log(this.name,this.phone,this.detail,this.sex,this.address)
		},
		Ms(){
			this.sex="女士"
		},
		Mi(){
			this.sex="先生"
		},
		chooseLocation(){
			uni.chooseLocation({
				success: (res) => {
					this.address=res.name
					this.obj= this.getArea(res.address)
				}
			})
		},
		getArea(str) {
			let area = {}
			let index11 = 0
			let index1 = str.indexOf("省")
			if (index1 == -1) {
			  index11 = str.indexOf("自治区")
			  if (index11 != -1) {
				area.Province = str.substring(0, index11 + 3)
			  } else {
				area.Province = str.substring(0, 0)
			  }
			} else {
			  area.Province = str.substring(0, index1 + 1)
			}
		 
			let index2 = str.indexOf("市")
			if (index11 == -1) {
			  area.City = str.substring(index11 + 1, index2 + 1)
			} else {
			  if (index11 == 0) {
				area.City = str.substring(index1 + 1, index2 + 1)
			  } else {
				area.City = str.substring(index11 + 3, index2 + 1)
			  }
			}
		 
			let index3 = str.lastIndexOf("区")
			if (index3 == -1) {
			  index3 = str.indexOf("县")
			  area.Country = str.substring(index2 + 1, index3 + 1)
			} else {
			  area.Country = str.substring(index2 + 1, index3 + 1)
			}
			return area;
		 }
	},
	onLoad(option) {
		console.log(option)
		if(option.id ==""){
			console.log("id为空")
		}else{
				this.id=option.id;
				
			addressDetail(option.id).then(data=>{
				console.log(data.data.data)
			
				this.name=data.data.data.real_name;
				this.phone=data.data.data.phone;
				this.detail=data.data.data.detail
				console.log(this.obj)
				
			})
		}
		
		
	}
}
